}

header {
    text-align: center;
    margin-bottom: 30px;
}

section {
    display: flex;
    width: 90%;
}

.background-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.photo-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(/static/media/montaniasbyn.2727a4e5.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
}

.glass-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 80%;
    max-width: 900px;
    height: 55px;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 10px;
    opacity: 0.7;
}

.glass-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.photo-heading {
    text-align: center;
    font-size: 3rem;
    color: #333333;
    margin: 0;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    text-align: center;
    min-height: 350px;
    align-items: center;
    color: #333333;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
}

h2 {
    display: flex;
    padding: 25px;
    margin-bottom: 35px;
}

main {
    text-align: center;
}

