    text-align: justify;
    font-size: 18px;
    margin-bottom: 20px;
}

.social-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.social-icons a {
    display: inline-block;
    margin: 80px 25px;
    font-size: 7rem;
    color: #333333;
    text-decoration: none;
}

.social-icons a:hover {
    color: #000000;
}

#welcome-message {
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
    background-color: #FFFFFF;
