}

.container {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
}

.content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.flex-container {
    width: 80%;
    margin-top: 75px;
    display: flex;
    align-items: center;
}

.text-container {
    flex: 1 1;
    padding: 20px;
}

.flex-container img {
    width: 50%;
    max-width: 500px;
}

@media (max-width: 768px) {
    .container {
        padding: 10px;
    }
    .photo-heading {
        text-align: center;
        font-size: 2rem;
        color: #333333;
        margin: 0;
    }
    h1 {
        font-size: 1rem;
    }
    p {
        font-size: 16px;
    }
    .flex-container {
        flex-direction: column;
        width: 100%;
    }
    .flex-container img {
        height: 500px;
        width: 100%;
    }
}

@media (max-width: 460px) {
    .container {
        padding: 10px;
        max-width: 100%;
    }
    .social-icons a {
        display: inline-block;
        margin: 80px 25px;
        font-size: 3rem;
        color: #333333;
        text-decoration: none;
    }
    .photo-heading {
        text-align: center;
        font-size: 1rem;
        color: #333333;
        margin: 0;
    }
    h1 {
        font-size: 1rem;
    }
    p {
        font-size: 16px;
    }
    .flex-container {
        flex-direction: column;
        width: 100%;
    }
    .flex-container img {
        height: 500px;
        width: 100%;
    }
}
