.container {
    margin: 0 auto;
}

h2 {
    text-align: center;
}

.success-message,
.error-message {
    margin-bottom: 10px;
    padding: 10px;
    text-align: center;
}

.success-message {
    background-color: #dff0d8;
    color: #3c763d;
}

.error-message {
    background-color: #f2dede;
    color: #a94442;
}

form {
    margin-top: 20px;
}

.form-group {
    margin-bottom: 20px;
}

input,
textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

button {
    display: block;
    margin: 0 auto;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}
.cookie-notice {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    opacity: 0;
    transition: opacity 0.3s;
}

.cookie-notice.show {
    opacity: 1;
}

.cookie-notice-content {
    background-color: #f5f5f5;
    padding: 20px;
    text-align: justify;
    max-width: 400px;
    border-radius: 4px;
}

.cookie-notice p {
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    color: #333333;
}

.cookie-notice a {
    color: #333333;
    text-decoration: underline;
}

.cookie-notice-buttons {
    display: flex;
    align-self: flex-end;
    margin: 15px;
}

button {
    margin: 5px;
}

.cookie-notice button {
    margin-right: 10px;
    padding: 10px 20px;
    background-color: #333333;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.cookie-notice button:hover {
    background-color: #555555;
}
